import {ExportDataExtractor} from "@component/export/export-data-extractor";
import {ExportContent} from "@component/export/export-content";
import {Downloader} from "@component/export/downloaders/downloader";
import {CsvDownloader} from "@component/export/downloaders/csv-downloader";
import {FileExtension} from "@component/export/files/file-extension";
import {FileUtils} from "@component/export/files/file-utils";
import * as JSZip from "jszip";
import {saveAs} from "file-saver";

/**
 * Zip builder. We can add multiple files, and then to download them into a zip.
 * @example:
 * ```ts
 * new ZipDownloader()
 *    .addCsv("hosts", hosts, hostDataExtractors)
 *    .addCsv("disks", disks, disksDataExtractors)
 *    .addPng("graph", imageContentBase64)
 *    .addSvg("graph", svgContent)
 *    .addTxt("txtFile", "Hello, world!")
 *    .download("bundle");
 * ```
 * Will create zip named bundle.zip with the following files hosts.csv, disks.csv, graph.png, graph.svg and txtFile.txt.
 */
export class ZipDownloader {
   private readonly zip: JSZip = new JSZip();
   private readonly csvDownloader: Downloader<any> = new CsvDownloader();

   private activeSubFolder: JSZip = null;
   private createdSubFolders: Map<string, JSZip> = new Map<string, JSZip>();

   // FILES
   addTxt(txtName: string, rawText: string): ZipDownloader {
      this.addFile(new RawFile(txtName, FileExtension.TXT, rawText));
      return this;
   }

   addCsvRaw(csvName: string, rawCsvContent: string): ZipDownloader {
      this.addFile(new RawFile(csvName, FileExtension.CSV, rawCsvContent));
      return this;
   }

   addCsv(csvName: string, items: any[], dataExtractors: ExportDataExtractor[]): ZipDownloader {
      this.addFile(new ExportedDataFile(csvName, items, dataExtractors, this.csvDownloader));
      return this;
   }

   addPng(imageName: string, imageContentBase64: string): ZipDownloader {
      this.addFile(new Base64ImageFile(imageName, FileExtension.PNG, imageContentBase64));
      return this;
   }

   addSvg(svgName: string, svgContent: string): ZipDownloader {
      this.addFile(new SvgFile(svgName, svgContent));
      return this;
   }

   private addFile(file: File): void {
      this.currentFolder.file(file.fileNameWithExtension(), file.content(), file.options());
   }

   // FOLDERS
   goToFolder(folderName: string): ZipDownloader {
      if (!folderName) {
         return this.goToRootFolder();
      }

      if (this.createdSubFolders.has(folderName)) {
         this.activeSubFolder = this.createdSubFolders.get(folderName);
      } else {
         this.activeSubFolder = this.zip.folder(folderName);
         this.createdSubFolders.set(folderName, this.activeSubFolder);
      }
      return this;
   }

   goToRootFolder(): ZipDownloader {
      this.activeSubFolder = null;
      return this;
   }

   private get currentFolder(): JSZip {
      return this.activeSubFolder != null ? this.activeSubFolder : this.rootFolder;
   }

   private get rootFolder(): JSZip {
      return this.zip;
   }

   // DOWNLOAD
   download(zipName: string): void {
      this.zip.generateAsync({type: "blob"})
            .then((zipContent: any) => saveAs(zipContent, this.getZipNameWithExtension(zipName)));
   }

   private getZipNameWithExtension(zipName: string): string {
      return FileUtils.buildFileNameWithExtension(zipName, FileExtension.ZIP, true);
   }
}

interface File {
   fileNameWithExtension(): string;
   content(): any;
   options(): JSZip.JSZipFileOptions;
}

class RawFile implements File {
   constructor(
         private fileName: string,
         private fileExtension: string,
         private rawContent: any) { }

   fileNameWithExtension = (): string => FileUtils.buildFileNameWithExtension(this.fileName, this.fileExtension);
   content = (): any => this.rawContent;
   options = (): JSZip.JSZipFileOptions => null;
}

class ExportedDataFile implements File {
   constructor(
         private fileName: string,
         private items: any[],
         private dataExtractors: ExportDataExtractor[],
         private downloader: Downloader<any>) { }

   fileNameWithExtension = (): string => this.downloader.buildFileNameWithExtension(this.fileName);
   content = (): any => this.downloader.formatContent(ExportContent.create(this.items, this.dataExtractors));
   options = (): JSZip.JSZipFileOptions => null;
}

class Base64ImageFile implements File {
   constructor(
         private imageName: string,
         private imageExtension: string,
         private imageContentBase64: string) { }

   fileNameWithExtension = (): string => FileUtils.buildFileNameWithExtension(this.imageName, this.imageExtension);
   content = (): any => this.imageContentBase64;
   options = (): JSZip.JSZipFileOptions => ({base64: true});
}

class SvgFile implements File {
   constructor(
         private svgName: string,
         private svgContent: string) { }

   fileNameWithExtension = (): string => FileUtils.buildFileNameWithExtension(this.svgName, FileExtension.SVG);
   content = (): any => this.svgContent;
   options = (): JSZip.JSZipFileOptions => ({binary: false});
}